import { useEffect } from 'react'
import styled from 'styled-components'
import { useLocation } from 'react-router-dom'

import CSSProperties from './cssProperties.json'

const CSSReference = () => {
  const location = useLocation()
  const hash = location.hash.substring(1, location.hash.length)

  useEffect(() => {
    if (document.getElementById(hash)) {
      setTimeout(() => window.scrollTo(0, document.getElementById(hash).offsetTop - 200), 100)
    }
  }, [hash])

  return (
    <ReferenceContainer>
      {CSSProperties.map(property => (
        <Row className="row" active={property.id === hash} id={property.id} key={property.id}><span>{property.name}</span><span>{property.description}</span></Row>
      ))}
    </ReferenceContainer>
  )
}

const Row = styled.div`
  display: flex;
  justify-content: space-between;
  line-height: 2rem;
  list-style: none;
  padding: .325rem 1.25rem;
  ${props => props.active ? 'box-shadow: 0 0 7px 3px #00000066; background-color: #740cff; color: white; font-weight: bold; border-top: 4px solid #ffffffe8; border-bottom: 4px solid #ffffffe8;' : ''};

  &:nth-child(even) {
    background-color: ${props => props.active ? '#740cff' : 'rgba(0, 0, 0, .15)'};
  }

  span {
    display: flex;
    flex: 12;

    &:first-child {
      flex: 4;
      font-weight: ${props => props.active ? 'bold' : 400};
    }
  }
`

const ReferenceContainer = styled.div`
  background-color: #f5f5f5;
  border-radius: .325rem;
  box-shadow: 0 0 2.25rem .325rem rgba(0, 0, 0, .25);
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  font-size: .9325rem;
  font-weight: 300;
  margin: 2rem;
`

export default CSSReference